<template>
    <div class="mht-body">
        <div v-for="(v,i) of types" :key="i" class="mht-menu">
            <div class="mht-img">
                <img :src="v.image" alt="">
            </div>
            <p class="mht-title">{{v.title}}</p>
        </div>
    </div>
</template>

<script>
    export default {
        computed:{
            types(){
                return this.$store.getters.types;
            }
        },
        mounted(){
            // 请求分类数据
            this.$store.dispatch("loadTypes");
        }
    }
</script>

<style lang="less">
    .mht-body{
        width: 100%;
        display: flex;
        flex-flow: row wrap;
        justify-content: space-around;
        .mht-menu{
            margin-top: 0.2rem;
            width: 17%;
            .mht-img{
                >img{
                    width: 100%;
                }
            }
            .mht-title{
                width: 100%;
                text-align: center;
            }
            }
    }
</style>



