import Vue from 'vue'
import App from './App'
import router from './router'
import VueAwesomeSwiper from "vue-awesome-swiper"
import store from './store'

import TopSearch from './globalComponents/TopSearch'
import BottomNavbar from './globalComponents/BottomNavbar'
import ProductList from './globalComponents/ProductList';


Vue.config.productionTip = false
Vue.use(VueAwesomeSwiper)

Vue.component("TopSearch",TopSearch);
Vue.component("BottomNavbar",BottomNavbar);
Vue.component("ProductList",ProductList);





new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



