<template>
    <div class="mtl-body">
        <!-- 左侧 -->
        <div class="mtl-left wrapper" ref="wrapper">
            <div>
                <MainTypeListAside :datas="typeList"></MainTypeListAside>
            </div>
        </div>
        <!-- 右侧 -->
        <div class="mtl-right wrappers" ref="wrappers">
            <div class="conta">
                <div>
                    <MainTypeListSection></MainTypeListSection>
                </div>
                <div>
                    <MainTypeListDetails></MainTypeListDetails>
                </div>

                <div class="cons">
                    <div v-for="(v,i) of typeDetails" :key="i" class="lists">
                        <MainTypeListProduct :datas="v"></MainTypeListProduct>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import MainTypeListAside from './MainTypeListAside';
    import MainTypeListSection from './MainTypeListSection';
    import MainTypeListDetails from './MainTypeListDetails';
    import MainTypeListProduct from './MainTypeListProduct'

    import Bscroll from 'better-scroll'

    // https://list.mogujie.com/search?page=1&pid=41888&q=
    // 右侧下部接口
    export default {
        components:{
            MainTypeListAside,
            MainTypeListSection,
            MainTypeListDetails,
            MainTypeListProduct
        },
        computed:{
            typeList(){
                return this.$store.getters.typeList;
            },
            typeDetails(){
                return this.$store.getters.typeDetails;
            }
        },
        methods:{
            bscrollInit(){
                this.$nextTick(()=>{
                    if(this.scroll1 && this.scroll2){
                        this.scroll1.refresh();
                        this.scroll2.refresh();
                    }else{
                        this.scroll1 = new Bscroll(this.$refs.wrapper,{
                            click:true
                        });
                        this.scroll2 = new Bscroll(this.$refs.wrappers,{
                            click:true
                        });
                    }
                });
            }
        },
        mounted(){
            this.bscrollInit();
            this.$store.dispatch("loadTypeList");    
        }
    }
</script>

<style lang="less">
    .mtl-body{
        font-size: 0.24rem;
        width: 100%;
        display: flex;
        flex-flow: row wrap;
        .mtl-left{
            width: 22%;
            background: rgb(241, 239, 239);
            position: absolute;
            left: 0;
            right: 78%;
            top: 0.861rem;
            bottom: 0.861rem;
        }
        .mtl-right{
            width: 78%;
            position: absolute;
            left: 22%;
            right: 0;
            top: 0.861rem;
            bottom: 0.861rem;
            .conta{
                width: 100%;
                .cons{
                    display: flex;
                    justify-content: space-around;
                    flex-flow: row wrap;
                    width: 100%;
                    .lists{
                        width: 45%;
                    }
                }
            }
        }
    }
</style>




