<template>
    <div class="mtls-body">
        <div v-for="(v,i) of typeShow" :key="i" class="con" @click="doSearch(v.title)">
            <router-link :to="`/details/${v.title}`" tag="div">
                <div class="pict">
                    <img :src="v.image" alt="">
                </div>
                <div class="mtis-text">
                    {{ v.title }}
                </div>
            </router-link>
        </div>
    </div>
</template>

<script>
    export default {
        computed:{
            typeShow(){
                return this.$store.getters.typeShow;                
            }
        },
        methods:{
            doSearch(v){
                this.$store.commit("setTitle",v);
            }
        },
        mounted(){
            
        }
    }
</script>

<style lang="less">
    .mtls-body{
        width: 100%;
        display: flex;
        flex-flow: row wrap;
        justify-content: left;
        .con{
            width: 33.33%;
            .pict{
                width: 100%;
                img{
                    margin-left: 15%;
                    width: 70%;
                    height: 1.1rem;;
                }
            }
            .mtis-text{
                margin-top: 0.3rem;
                padding-bottom: 0.3rem;
                width: 100%;
                text-align: center;
            }
        }
    }
</style>



