<template>
    <div class="mhht-body">
        <div class="mhht-title">热门分类</div>
        <div class="mhht-hot"> 
            <div v-for="(v,i) of hotTypes" :key="i" class="mhht-hottype">
                <div>
                    <img :src="v.image" alt="">
                </div>
                <p>
                    {{ v.title }}
                </p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        computed:{
            hotTypes(){
                return this.$store.getters.hotTypes;
            }
        },
        mounted(){
            // 热门分类
            this.$store.dispatch("loadHotTypes");
        }
    }
</script>

<style lang="less">
    .mhht-body{
        margin-top: 0.2rem;
        width: 100%;
        .mhht-title{
            width: 100%;
            background: url('https://s10.mogucdn.com/p1/160415/upload_ifrwkyzymu2damdgg4zdambqhayde_313x10.png') no-repeat;
            background-size:3.13rem;
            background-position: 50%;
            text-align: center;
        }
        .mhht-hot{
            display: flex;
            justify-content: space-around;
            flex-flow: row wrap;
            .mhht-hottype{
                margin-top: 0.2rem;
                width: 22%;
                >div{
                    width: 100%;
                    >img{
                        width:80%;   
                        margin-left: 10%;
                    }
                }
                >p{
                    color:gray;
                    font-weight: 500;
                    text-align: center;
                }
            }
        }
    }
</style>



