<template>
    <div class="pd-body">
        <div class="pd-wrap" ref="wrp">
            <div class="pd-content">
                <div class="pd-img">
                    <img :src="product.img" alt="">
                </div>
                <div class="pd-title">
                    {{ product.title }}
                </div>
                <div class="pd-price">
                    <div class="pd-pro">
                        ￥{{ product.price }}
                    </div>
                    <div class="pd-org">
                        <span class="pd-pri">￥{{ product.orgPrice }}</span>
                        <br>
                        <span class="pd-cheap">超实惠</span>
                    </div>
                </div>
                <div class="pd-else">
                    免邮费
                </div>
                <div class="pd-ele">
                    <div class="pd-li">
                        <div class="pd-li-img">
                            <img src="https://s10.mogucdn.com/mlcdn/c45406/180417_25kbfg1c3hdbd120394ji4b11bk2k_36x36.png_60x60.webp" alt="">         
                        </div>
                        <div class="pd-txt">
                            7天无理由退货      
                        </div>                  
                    </div>
                    <div class="pd-li">
                        <div class="pd-li-img">
                            <img src="https://s10.mogucdn.com/mlcdn/c45406/180417_25kbfg1c3hdbd120394ji4b11bk2k_36x36.png_60x60.webp" alt="">         
                        </div>
                        <div class="pd-txt">
                            退货补邮费   
                        </div>
                                            
                    </div>
                    <div class="pd-lis">
                        >
                    </div>
                </div>
                <div class="pd-comment">
                    <div class="pd-tt">热门评论</div>
                    <div v-for="(v,i) of comment" :key="i" class="pd-com">
                        <div class="pd-uname">
                            <div class="pd-img">
                                <img :src="img" alt="" v-if="!v.images.length">
                                <img :src="m" alt="" v-for="(m,n) of v.images" :key="n">
                            </div>
                            <div class="pd-name">
                                {{ v.user.uname }}
                            </div>
                        </div>
                        <div class="pd-contain">
                            {{ v.content }}
                        </div>
                        <div class="pd-info">
                            <span class="pd-text">{{ v.time }}</span>
                            <span v-for="(t,s) of v.extraInfo" :key="s" class="pd-text">{{ t }}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="pd-bottom">
            <div class="pd-cons">
                <div class="pd-col">
                    <div>
                        <i class="iconfont icon-ziyuan"></i>
                    </div>
                    <p>
                        收藏
                    </p>
                </div>
                <div class="pd-col">
                    <div>
                        <i class="iconfont icon-ziyuan"></i>
                    </div>
                    <p>
                        小店
                    </p>
                </div>
                <div @click="addToCol()" class="pd-shop">
                    加入购物车
                </div>
                <div class="pd-now">
                    立即购买
                </div>
            </div> 
        </div>
    </div>
</template>

<script>
    import Bscroll from "better-scroll"

    export default {
        data(){
            return {
                img:"https://s17.mogucdn.com/mlcdn/c45406/180426_14005gg6hbl9chj38a22130a81f3d_960x1280.jpg_100x100.webp"
            }
        },
        computed:{
            product(){
                return this.$store.getters.product;
            },
            comment(){
                return this.$store.getters.comment;
            }
        },
        methods:{
            bscrollInit(){
                this.$nextTick(()=>{
                    if(this.scroll){
                        this.scroll.refresh();
                    }else{
                        this.scroll = new Bscroll(this.$refs.wrp,{
                            click:true
                        });
                    }
                });
            },
            addToCol(){
                this.$store.commit("setMyShop",this.product);
            }
        },
        mounted(){
            this.bscrollInit();
            this.$store.commit("setItemId",this.product.tradeItemId);
            this.$store.dispatch("loadProductDetails");
        }
    }
</script>

<style lang="less">
    .pd-body{
        width: 100%;
        .pd-wrap{
            background-color: rgb(232, 229, 229);
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0.821rem;
            .pd-content{
                width: 100%;
                background-color: #fff;
                .pd-img{
                    width: 100%;
                    height: 7.5rem;
                    >img{
                        width: 100%;
                    }
                }
                .pd-title{
                    width: 100%;
                    font-size: 0.25rem;
                    box-sizing: border-box;
                    padding:0.2rem 0.3rem 0 0.3rem;
                }
                .pd-price{
                    width: 100%;
                    height: 1rem;
                    display: flex;
                    flex-flow: row nowrap;
                    .pd-pro{
                        text-indent: 0.1rem;
                        color: red;
                        line-height: 1rem;
                        height: 1rem;  
                        font-size: 0.8rem;                   
                    }
                    .pd-org{
                        height: 1rem;
                        font-size: 0.3rem;
                        .pd-pri{
                            width: 1rem;
                            text-align: center;
                            height: 0.4rem;
                            line-height: 0.6rem;
                            display: inline-block;
                            color: rgb(156, 152, 152);
                            text-decoration: line-through;
                        }
                        .pd-cheap{
                            height: 0.4rem;
                            width: 1.2rem;
                            line-height: 0.4rem;
                            font-size: 0.25rem;
                            margin-top: -0.05rem;
                            text-align: center;
                            background-color: rgb(255,232,238);
                            display: inline-block;
                            color: red;
                        }
                    }
                }
                .pd-else{
                    text-indent: 0.25rem;
                    font-size: 0.15rem;
                    color: grey;
                }
                .pd-ele{
                    color: gray;
                    width: 100%;
                    font-size: 0.25rem;
                    display: flex;
                    height: 0.8rem;
                    margin-left: 0.25rem;
                    box-sizing: border-box;
                    border-top: 0.01rem solid #ccc;
                    padding: 0.2rem 0;
                    .pd-li{
                        width: 45%;
                        height: 0.4rem;
                        display: flex;
                        .pd-li-img{
                            width: 0.35rem;
                            >img{
                                width: 0.3rem;
                                vertical-align: middle;
                            }
                        }
                        .pd-txt{
                            text-indent: 0;
                            width: 2.2rem;
                        }
                    } 
                    .pd-lis{
                        font-size: 0.35rem;
                        height: 0.4rem;
                        line-height: 0.4rem;
                    }                           
                }
                .pd-comment{
                    width: 100%;
                    font-size: 0.24rem;
                    .pd-tt{
                        width: 100%;
                        box-sizing: border-box;
                        font-size: 0.3rem;
                        color: rgb(158, 159, 159);
                        padding: 0.1rem 0.2rem;
                        background-color: rgb(234, 233, 233);
                    }
                    .pd-com{
                        width: 100%;
                        border-bottom: 0.01rem solid #ccc;
                        .pd-uname{
                            font-size: 0.3rem;
                            padding: 0.1rem 0.2rem;
                            box-sizing: border-box;
                            color: rgb(95, 92, 92);
                            display: flex;
                            flex-flow:row nowrap;
                            .pd-img{
                                width: 15%;
                                height: 0.7rem;
                                >img{
                                    margin-left: 10%;
                                    width: 82%;
                                    height: 0.7rem;
                                    border-radius: 0.35rem;
                                }
                            }
                            .pd-name{
                                width: 83%;
                                margin-left: 2%;
                                height: 0.7rem;
                                line-height: 0.7rem;
                            }
                        }
                        .pd-contain{
                            font-size: 0.25rem;
                            padding: 0.1rem 0.2rem;
                            box-sizing: border-box;
                            color: rgb(146, 142, 142);
                        }
                        .pd-info{
                            font-size: 0.2rem;
                            padding: 0.1rem 0.2rem;
                            box-sizing: border-box;
                            color: rgb(146, 142, 142);
                            .pd-text{
                                display: inline-block;
                                margin-right: 0.1rem;
                            }
                        }
                    }
                }
            }
        }
        .pd-bottom{
            width: 100%;
            position: fixed;
            top: 100%;
            box-sizing: border-box;
            border-top: 0.01rem solid #ccc;
            margin-top: -0.821rem;
            font-size: 0.24rem;
            .pd-cons{
                width: 100%;
                display: flex;
                flex-flow: row nowrap;
                height: 0.82rem;
                .pd-col{
                    width: 18%;
                    height: 0.82rem;
                    text-align: center;
                    line-height: 0.42rem;
                    box-sizing: border-box;
                    border-right: 0.01rem solid #ccc;
                }
                .pd-shop{
                    width: 32%;
                    text-align: center;
                    height: 0.82rem;
                    line-height: 0.82rem;
                    box-sizing: border-box;
                    background: pink;
                    font-size: 0.3rem;
                    color: red;
                }
                .pd-now{
                    width: 32%;
                    text-align: center;
                    font-size: 0.3rem;
                    height: 0.82rem;
                    line-height: 0.82rem;
                    background: rgb(229, 62, 62);
                    color: white;
                }
            }
        }
    }
</style>



