<template>
    <div class="pl-body" @click="saveDel()">
        <router-link to="/product" tag="div">
            <div class="pl-list">
                <img :src="datas.img" alt="">
            </div>
            <p class="pl-intro" v-if="flag">{{ datas.title }}</p>
            <p class="pl-prop" v-if="!flag">
                <span v-for="(v,i) of datas.props" :key="i">
                    {{ v }}
                </span>
                </p>
            <div class="pl-price">
                <p class="pl-left">￥{{ datas.price }}</p>
                <p class="pl-right">
                    <span>{{ datas.cfav }}</span>
                    <i class="iconfont icon-ziyuan"></i>
                </p>
            </div>
        </router-link>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                flag:true
            }
        },
        props:{
            datas:null
        },
        methods:{
            saveDel(){
                this.$store.commit("setProduct",this.datas);
            }
        },
        mounted(){
            if(this.$route.path=="/main/home"){
                this.flag =true
            }else{
                this.flag =false;
            }
        }
    }
</script>

<style lang="less">
    .pl-body{
        margin-top: 0.1rem;
        width: 80%;
        margin-left: 10%;
        .pl-list{
            width: 100%;
            >img{
                height: 4rem;
                width: 100%;
                border-radius: 0.1rem;
            }
        }
        .pl-intro{
            width: 100%;
            color: rgb(88, 86, 86);
            font-size: 0.2rem;
            text-indent: 0.2rem;
            height: 0.55rem;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
        }
        .pl-prop{
            height: 0.55rem;
            width: 100%;
            color: rgb(88, 86, 86);
            font-size: 0.2rem;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
        }
        .pl-price{
            width: 100%;
            .pl-left{
                color: red;
                display: inline-block;
                width: 47%;
                height: 0.3rem;
                text-align: left;
                font-weight: bold;
            }
            .pl-right{
                display: inline-block;
                width: 47%;
                text-align: right;
                color: gray;
                font-size: 0.22rem;
                height: 0.3rem;
                .iconfont{
                    font-size: 0.2rem;
                    opacity: 0.8;
                }
            }
        }
    }
</style>



