<template>
    <div class="mhs-body">
        <swiper :options="swiperOption" ref="mySwiper">
            <swiper-slide v-for="(v,i) of imgUrl" :key="i">
                <img :src="v.image_800" alt="">
            </swiper-slide>
            <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                swiperOption:{
                    autoplay:2000,
                    loop:true,
                    pagination:".swiper-pagination"
                }
            }
        },
        computed:{
            imgUrl(){
                return this.$store.getters.imgUrl;
            }
        },
        mounted(){
            // 请求轮播图片
            this.$store.dispatch("loadImgUrl");
        }
    }
</script>

<style lang="less">
    .mhs-body{
        img{
            width: 100%;
        }
    }
</style>



