<template>
    <div class="mhb-body">
        <div class="mhb-title">超实惠</div>
        <div class="mhb-content">
            <div v-for="(v,i) of berefit" :key="i" class="mhb-cheaper">
                <div class="mhb-texts">
                    <span class="mhb-bolder">{{ v.title }}</span>
                    <br>
                    <span class="mhb-hot">{{ v.viceTitle.substring(1,v.viceTitle.length-1) }}</span>
                </div>
                <div class="mhb-pic">
                    <img :src="v.image" alt="">
                </div>
            </div> 
            <div class="mhb-clear"></div>
        </div>
    </div>
</template>

<script>
    export default {
        computed:{
            berefit(){
                return this.$store.getters.berefit;
            }
        },
        mounted(){
            // 超实惠数据
            this.$store.dispatch("loadBerefit");
        }
    }
</script>

<style lang="less">
    .mhb-body{
        margin-top: 0.2rem;
        width: 100%;
        .mhb-title{
            width: 100%;
            background: url('https://s10.mogucdn.com/p1/160415/upload_ifrwkyzymu2damdgg4zdambqhayde_313x10.png') no-repeat;
            background-size:3.13rem;
            background-position: 50%;
            text-align: center;
        }
        .mhb-content{
            margin-top: 0.2rem;
            width: 100%;
            font-size: 0.2rem;
            .mhb-cheaper{
                border: 1px solid #ccc;
                padding:0.1rem 0.1rem;
                box-sizing: border-box;
                float: left;
                width: 33.33%;
                text-align: center;
                height: 3rem;
                line-height: 0.4rem;
                .mhb-texts{
                    .mhb-bolder{
                        font-size: 0.23rem;
                    }
                    .mhb-hot{
                        color: #ff5f7e;
                    }
                }
                .mhb-pic{
                    width: 100%;
                    >img{
                        width: 100%;
                    }
                }
            }
            .mhb-cheaper:first-child{
                width: 50%;
                .mhb-pic{
                    width: 100%;
                    img{
                        width: 70%;
                    }
                }
            }
            .mhb-cheaper:nth-child(2){
                width: 50%;
                height: 1.5rem;
                .mhb-texts{
                    text-align: left;
                    width: 56%;
                    display: inline-block;
                    height: 100%;
                }
                .mhb-pic{
                    display: inline-block;
                    width: 42%;
                    height: 100%;
                    >img{
                        width: 100%;
                    }
                }
            }
            .mhb-cheaper:nth-child(3){
                width: 50%;
                height: 1.5rem;
                .mhb-texts{
                    text-align: left;
                    width: 56%;
                    display: inline-block;
                    height: 100%;
                }
                .mhb-pic{
                    display: inline-block;
                    width: 42%;
                    height: 100%;
                    >img{
                        width: 100%;
                    }
                }
            }
            .mhb-clear{
                clear: both;
            }
        }
    }
</style>



