import jsonp from "../api"

const actions ={
    // 轮播图
    loadImgUrl({commit,state}){
        if(state.imgUrl.length == 0){
            jsonp.findDatasByPids({
                appPlat:"m",
                pids:"51822"
            }).then(({data})=>{
                commit("setImgUrl",data["51822"]["list"]);
            })
        }      
    },
    // 主页类型
    loadTypes({commit,state}){
        if(state.types.length == 0){
            jsonp.findDatasByPids({
                appPlat:"m",
                pids:"106930"
            }).then(({data})=>{
                commit("setTypes",data["106930"]["list"]);
            })
        }  
    },
    // 主页实惠
    loadBerefit({commit,state}){
        if(state.berefit.length == 0){
            jsonp.findDatasByPids({
                appPlat:"m",
                pids:"51833"
            }).then(({data})=>{
                commit("setBerefit",data["51833"]["list"]);
            })
        }   
    },
    loadHotTypes({commit,state}){
        if(state.hotTypes.length == 0){
            jsonp.findDatasByPids({
                appPlat:"m",
                pids:"51836"
            }).then(({data})=>{
                commit("setHotTypes",data["51836"]["list"]);
            })
        }  
    },
    // 主页喜欢
    loadLikes({commit,state}){
        if(state.likes.length == 0){
            jsonp.findDatasByParams({
                pid:"9750",
                sort:"pop",
                cKey:46
            }).then(({result})=>{
                commit("setLikes",result["wall"]["docs"]);
            })
        }  
    },
    // 分类左侧列表
    loadTypeList({commit,state}){
        if(state.typeList.length == 0){
            jsonp.findDatasByPids({
                appPlat:"m",
                pids:"41789"
            }).then(({data})=>{
                commit("setTypeList",data["41789"]["list"]);
                commit("setPid",data["41789"]["list"][0]["maitKey"]);
                commit("setSortId",data["41789"]["list"][0]["miniWallkey"]);
                this.dispatch("loadTypeShow");
                this.dispatch("loadTypeDetails");
            })
        }  
    },
    // 分类右侧上方分类
    loadTypeShow({commit,state}){
        jsonp.findDatasByPid({
            appPlat:"m",
            pid:state.pid
        }).then(({data})=>{
            commit("setTypeShow",data["categoryNavigation"]["list"]);
        })
    },
    // 分类详情
    loadTypeDetails({commit,state}){
        jsonp.findDatasByParams({
            fcid:state.sortId,
            page:1,
            sort:state.sort,
            cKey:"h5-cube"
        }).then(({result})=>{
            commit("setSortName",result["sortFilter"]);
            commit("setTypeDetails",result["wall"]["docs"]);
        })
    },
    // 关键词搜索
    loadKeyData({commit,state}){
        if(state.hotSearch.length!=0){
            return
        }
        jsonp.findDataByKey({
            platform:"H5",
            code:"hotWord,sketch"
        }).then(({data:{hotWord,sketch}})=>{
            commit("setDefaultWord",sketch.data.frontword);
            commit("setHotSearch",hotWord.data);
        })
    },
    // 加载搜索详情页
    loadSearchDetails({commit,state}){
        jsonp.findDatasByParams({
            q:state.question,
            page:1,
            sort:state.sorts,
            cKey:46
        }).then(({result})=>{   
            commit("setSortsName",result["sortFilter"]);
            commit("setSorts",result["sortFilter"][0]["sortKey"]);
            commit("setSearchDetails",result["wall"]["docs"]);
        })
    },
    loadSearchDetail({commit,state}){
        jsonp.findDatasByParams({
            fcid:state.pid,
            page:1,
            title:state.title,
            sort:state.sorts,
            cKey:16
        }).then(({result})=>{   
            commit("setSortsName",result["sortFilter"]);
            commit("setSorts",result["sortFilter"][0]["sortKey"]);
            commit("setSearchDetails",result["wall"]["docs"]);
        })
    },
    // 加载评论
    loadProductDetails({commit,state}){
        jsonp.findCommentById({
            type:1,
            itemId:state.itemId,
            appPlat:'m'
        }).then(({data})=>{
            commit("setComment",data["list"]);
        })
    }
    
}

export default actions;


