<template>
    <div class="msh-body">
        <div class="msh-history">
            <div class="msh-left">
                <i class="iconfont icon-icon--"></i>
                <span>历史搜索</span>
            </div>
            <div class="msh-right">
                <i class="iconfont icon-lajixiang" @click="clearHistory()"></i>
            </div>
        </div>
        <div class="msh-history-back">
            <div v-for="(v,i) of historyData" :key="i" class="msh-history-details" @click="doSearch(v)">
                <router-link :to="`/details/${v}`" tag="div">
                    {{ v }}
                </router-link>
               
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        computed:{
            historyData(){
                return Array.from(this.$store.getters.historyData);
            }
        },
        methods:{
            clearHistory(){
                this.$store.commit("clearHistoryData","");
            },
            doSearch(v){
                this.$store.commit("setQuestion",v);
            }
        }
    }
</script>

<style lang="less">
    .msh-body{
        width: 100%;
        margin-top: 0.2rem;
        padding-bottom: 0.2rem;
        border-bottom: 0.02rem solid lightgray;
        font-size: 0.24rem;
        color: rgb(136, 131, 131);
        .msh-history{
            width: 100%;
            display: flex;
            flex-flow: row nowrap;
            .msh-left{
                text-indent: 5%;
                width: 50%;
            }
            .msh-right{
                width: 40%;
                text-align: right;
            }
        }
        .msh-history-back{
            width: 86%;
            margin-left:7%;
            display: flex;
            flex-flow: row wrap;
            .msh-history-details{
                padding: 0.02rem 0.1rem;
                margin:0.2rem  0.15rem 0 0;
                height: 0.3rem;
                border-radius: 0.1rem;
                border: 0.02rem solid lightgray;
            }
        }
    }
</style>



