import Vue from 'vue'
import Router from 'vue-router'


import MainContent          from '../components/MainContent'
import MainHome             from '../components/MainHome'
import MainTypeList         from "../components/MainTypeList"

import MainSearch           from '../components/MainSearch'

import SearchDetails from '../components/SearchDetails'

import ProductionDetails from '../components/ProductionDetails'

import SimpleContent        from "../components/SimpleContent"
import SimpleInformation    from "../components/SimpleInformation"
import SimpleShop           from "../components/SimpleShop"


Vue.use(Router)

export default new Router({
    mode:"history",
    linkExactActiveClass:"active",
    routes: [
        {path:"/",redirect:"/main"},
        {path: '/main',component: MainContent,children:[
            {path:"",redirect:"home"},
            {path:"home",name:"home",component:MainHome},
            {path: 'types',component: MainTypeList}
        ]},
        {path:"/simple",name:"simple",component:SimpleContent,children:[
            {path:"shop",name:"shop",component:SimpleShop},
            {path:"info",name:"info",component:SimpleInformation}      
        ]},
        {path:"/search",name:"search",component:MainSearch},
        {path:"/details/:id",name:"details",component:SearchDetails},
        {path:"/product",name:"product",component:ProductionDetails}
    ]
})



