<template>
    <div class="mtld-body">
        <div v-for="(v,i) of sortName" :key="i" @click="changeSort(v.sortKey,i)" :class="{ eve:true , dct : aa==i}">
            {{ v.title }}
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                aa:0
            }
        },
        computed:{
            sortName(){
                return this.$store.getters.sortName;
            }
        },
        methods:{
            changeSort(par,i){
                this.aa = i;
                this.$store.commit("setSort",par);
                this.$store.dispatch("loadTypeDetails");
            }
        },
        mounted(){
        }
    }
</script>

<style lang="less">
    .mtld-body{
        margin-top: 0.3rem;
        width: 100%;
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        .eve{
            flex-grow: 1;
            text-align: center;
            height: 0.6rem;
            line-height: 0.6rem;
            font-size: 0.3rem;
            border: 0.01rem solid lightgray;
        }
        .dct{
            color: red;
        }
    }
</style>



