<template>
    <div class="si-body">
        <div class="siwrapper" ref="siwrapper">
            <SimpleInfomationList></SimpleInfomationList>
        </div>
        <BottomNavbar></BottomNavbar>
    </div>
</template>

<script>
    import Bscroll from 'better-scroll'

    import SimpleInfomationList from './SimpleInfomationList';

    export default {
        components:{
            SimpleInfomationList
        },
        methods:{
            bscrollInit(){
                this.$nextTick(()=>{
                    if(this.scroll3){
                        this.scroll3.refresh();
                    }else{
                        this.scroll3 = new Bscroll(this.$refs.siwrapper,{
                            click:true
                        });
                    }
                });
            }
        },
        mounted(){
            this.bscrollInit();
        }
    }   
</script>

<style lang="less">
    .si-body{
        width: 100%;
        .siwrapper{
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0.821rem;
            background: rgb(235, 233, 233);
        }
    }
</style>



