<template>
    <div class="ss-body">
        <div class="ss-nav">
            <div class="ss-nav-left">
                <i class="iconfont icon-xiangzuo" @click="goback()"></i>
            </div>
            <div class="ss-nav-num">
                购物车（{{0}}）
            </div>
            <div class="ss-nav-right">
                <span>编辑</span>
                <i class="iconfont icon-iconfontzhizuobiaozhun023110"></i>
            </div>
        </div>
        <div class="ss-shopping" ref="wrap">
            <div class="ss-st">
                <div v-for="(v,i) of shopping" :key="i" class="ss-shopDel">
                    <div class="ss-check">
                        <input type="checkbox" v-model="v.checked" @change="selectCheck()">
                    </div>
                    <div class="ss-img">
                        <img :src="v.img" alt="">
                    </div>
                    <div class="ss-tit">
                        <div class="ss-ss">
                            {{ v.title }}
                        </div>
                         <div class="ss-pri">
                            <span>￥{{ v.price }}</span>  
                            <span>￥{{ v.orgPrice }}</span>  
                        </div>
                    </div>
                   
                </div>
            </div>
        </div>
        <div class="ss-footer">
            <div class="ss-checkbox">
                <input type="checkbox" v-model="all" @change="changeAll()">
            </div>
            <div class="ss-add-all">
                全选（0）<span class="ss-money">￥{{ money }}</span>  
            </div>
            <div class="ss-calculate">
                <button>去结算</button>
            </div>
        </div>
    </div>
</template>

<script>
    import Bscroll from 'better-scroll'

    export default {
        data(){
            return {
                all:false
            }
        },
        computed:{
            shopping(){
                return this.$store.getters.myShop;
            },
            money(){
                let nums = 0;
                this.shopping.forEach((v)=>{
                    if(v.checked){
                        nums+=v.price;
                    }
                })
                return nums;
            }
        },
        methods:{
            bscrollInit(){
                this.$nextTick(()=>{
                    if(this.scroll4){
                        this.scroll4.refresh();
                    }else{
                        this.scroll4 = new Bscroll(this.$refs.wrap,{
                            click:true
                        });
                    }
                });
            },
            goback(){
                this.$router.go(-1);
            },
            changeAll(){
                this.shopping.forEach(v => {
                    v.checked = !this.all;
                });
            },
            selectCheck(){
                this.all = true;
                for(let i = 0; i<this.shopping.length;i++){ 
                    if(this.shopping[i].checked){
                        continue;
                    }
                    this.all = this.shopping[i].checked;   
                }
            }
        },
        mounted(){
            this.bscrollInit();
        }
    }
</script>

<style lang="less">
    .ss-body{
        width: 100%;
        .ss-nav{
            box-sizing: border-box;
            width: 100%;
            height: 0.821rem;
            display: flex;
            flex-flow: row nowrap;
            border-bottom: 0.01rem solid #ccc;
            .ss-nav-left{
                width: 20%;
                line-height: 0.41rem;
                text-indent: 0.2rem;
                .iconfont{
                    font-size: 0.4rem;
                }
            }
            .ss-nav-num{
                line-height: 0.82rem;
                text-align: center;
                width: 60%;
                font-size: 0.4rem;
            }
            .ss-nav-right{
                font-size: 0.28rem;
                width: 20%;
                line-height: 0.82rem;
            }
        }
        .ss-shopping{
            position: absolute;
            top: 0.821rem;
            left: 0;
            right: 0;
            bottom: 0.821rem;
            .ss-st{
                width: 100%;
                .ss-shopDel{
                    width: 100%;
                    box-sizing: border-box;
                    margin: 0.2rem 0;
                    border: 0.01rem solid #ccc;
                    display: flex;
                    flex-flow: row nowrap;
                    font-size: 0.3rem;
                    .ss-check{
                        line-height: 2rem;
                        width: 10%;
                        height: 2rem;
                        text-align: center;
                    }
                    .ss-img{
                        width: 20%;
                        height: 2rem;
                        >img{
                            width: 80%;
                            margin-top: 0.25rem;
                            height: 1.5rem;
                            margin-left: 10%;
                        }
                    }
                    .ss-tit{
                        width: 70%;
                        height: 2rem;
                        .ss-ss{
                            height: 1.35rem;
                            width: 100%;
                            font-size: 0.3rem;
                            box-sizing: border-box;
                            padding: 0.2rem 0.2rem;
                            color: gray;
                        }
                        .ss-pri{
                            width: 100%;
                            box-sizing: border-box;
                            padding: 0 0.2rem;
                            >span:first-child{
                                color:red;
                            }
                            >span:last-child{
                                font-size: 0.25rem;
                                color: grey;
                                text-decoration: line-through;
                            }
                        }
                    }
                }
            }
        }
        .ss-footer{
            position: absolute;
            box-sizing: border-box;
            border-top: 0.01rem solid #ccc;
            top: 100%;
            margin-top: -0.821rem;
            width: 100%;
            height: 0.821rem;
            display: flex;
            flex-flow:row nowrap;
            font-size: 0.3rem;
            .ss-checkbox{
                width: 15%;
                >input{
                    margin-left: 0.4rem;
                    margin-top: 0.21rem;
                    width: 0.4rem;
                    height: 0.4rem;
                }
            }
            .ss-add-all{
                line-height: 0.82rem;
                width: 51%;
                .ss-money{
                    color: red;
                }
            }
            .ss-calculate{
                width: 30;
                >button{
                    background-color: red;
                    margin-top: 0.1rem;
                    width: 1.8rem;
                    height: 0.621rem;
                    font-size: 0.3rem;
                    color: white;
                    font-weight: 500;
                    border-radius: 0.2rem;
                }
            }
        }
    }
</style>



