<template>
    <div class="mhl-body">
        <div class="mhl-title">猜你喜欢</div>
        <div class="mhl-content">
            <div v-for="(v,i) of likes" :key="i" class="mhl-every">
                <ProductList :datas="v"></ProductList>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        computed:{
            likes(){
                return this.$store.getters.likes;
            }
        },
        mounted(){
            // 猜你喜欢的数据
            this.$store.dispatch("loadLikes");
        }
    }
</script>

<style lang="less">
    .mhl-body{
        margin-top: 0.2rem;
        width: 100%;
        .mhl-title{
            width: 100%;
            background: url('https://s10.mogucdn.com/p1/160415/upload_ifrwkyzymu2damdgg4zdambqhayde_313x10.png') no-repeat;
            background-size:3.13rem;
            background-position: 50%;
            text-align: center;
        }
        .mhl-content{
            margin-top: 0.2rem;
            display: flex;
            justify-content: left;
            flex-flow: row wrap;
            .mhl-every{
                width: 50%;
            }
        }
    }
</style>



