<template>
    <div class="mtlp-body" @click="doSearch()">
        <router-link to="/product" tag="div">
            <div class="mtlp-list">
                <img :src="datas.img" alt="">
            </div>
            <p class="mtlp-intro">{{ datas.title }}</p>
            <div class="mtlp-price">
                <p class="mtlp-left">￥{{ datas.price }}</p>
                <p class="mtlp-right">
                    <span>{{ datas.cfav }}</span>
                    <i class="iconfont icon-ziyuan"></i>
                </p>
            </div>
        </router-link>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                flag:true
            }
        },
        props:{
            datas:null
        },
        methods:{
            doSearch(){
                this.$store.commit("setProduct",this.datas);
            }
        },
        mounted(){
            if(this.$route.path=="/main/home"){
                this.flag =true
            }else{
                this.flag =false;
            }
        }
    }
</script>

<style lang="less">
    .mtlp-body{
        margin-top: 0.1rem;
        width: 90%;
        margin-left: 5%;
        .mtlp-list{
            width: 100%;
            >img{
                height: 3rem;
                width: 100%;
                border-radius: 0.1rem;
            }
        }
        .mtlp-intro{
            width: 100%;
            color: rgb(88, 86, 86);
            font-size: 0.2rem;
            text-indent: 0.2rem;
            height: 0.55rem;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
        }
        .mtlp-price{
            width: 100%;
            .mtlp-left{
                color: red;
                display: inline-block;
                width: 47%;
                height: 0.3rem;
                text-align: left;
                font-weight: bold;
            }
            .mtlp-right{
                display: inline-block;
                width: 47%;
                text-align: right;
                color: gray;
                font-size: 0.22rem;
                height: 0.3rem;
                .iconfont{
                    font-size: 0.2rem;
                    opacity: 0.8;
                }
            }
        }
    }
</style>



