
const mutations = {
    setImgUrl(state,data){
        state.imgUrl = data;
    },
    setTypes(state,data){
        state.types =data;
    },
    setBerefit(state,data){
        state.berefit = data;
    },
    setHotTypes(state,data){
        state.hotTypes = data;
    },
    setLikes(state,data){
        state.likes = data;
    },
    setTypeList(state,data){
        state.typeList = data;
    },
    setTypeShow(state,data){
        state.typeShow = data;
    },
    setTypeDetails(state,data){
        state.typeDetails = data;
    },
    setPid(state,data){
        state.pid = data;
    },
    setSort(state,data){
        state.sort = data;
    },
    setSortName(state,data){
        state.sortName = data;
    },
    setSortId(state,data){
        state.sortId = data;
    },
    setShopping(state,data){
        state.shopping = data;
    },
    setHistoryData(state,data){
        for( let v of state.historyData){
            if( v == data){
                return
            }
        }
        state.historyData.push(data);
    },
    clearHistoryData(state,data){
        state.historyData = [];
    },
    setDefaultWord(state,data){
        state.defaultWord = data;
    },
    setHotSearch(state,data){
        state.hotSearch = data;
    },
    setQuestion(state,data){
        state.question = data;
    },
    setSorts(state,data){
        state.sorts = data;
    },
    setSortsName(state,data){
        state.sortsName = data;
    },
    setSearchDetails(state,data){
        state.searchDetails = data;
    },
    setProduct(state,data){
        state.product = data;
    },
    setItemId(state,data){
        state.itemId = data;
    },
    setComment(state,data){
        state.comment = data;
    },
    setMyShop(state,data){
        state.myShop.push(data);
    },
    setTitle(state,data){
        state.title = data;
    }
}

export default mutations;


