<template>
    <div class="ts-body">
        <div class="ts-info">
            <!-- 只有在主页才会显示气泡 -->
            <i class="iconfont icon-iconfontzhizuobiaozhun023110" v-if="flag"></i>
            <i class="iconfont icon-xiangzuo" v-if="!flag" @click="returnBack()"></i>
        </div>
        <div class="ts-search">
            <router-link to="/search" tag="div">
                <i class="iconfont icon-icon--"></i>
                <!-- 如果是搜索记录则显示为搜索记录，否则为默认关键词 -->
                <span>{{ !!history?history:defaultWord }}</span>
            </router-link>
        </div>
        <div class="ts-shop">
            <i class="iconfont icon-gouwuche"></i>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                flag:true,
                // 接收的历史记录参数
                history:this.$route.params.id
            }
        },
        computed:{
            defaultWord(){
                return this.$store.getters.defaultWord;
            }
        },
        methods:{
            returnBack(){
                this.$router.go(-1);
            }
        },
        mounted(){
            if(this.$route.path=="/main/home"){
                this.flag =true
            }else{
                this.flag =false;
            }
            // 页面构建完成请求关键词
            this.$store.dispatch("loadKeyData");
        }
    }
</script>

<style lang="less">
    .ts-body{
        box-sizing: border-box;
        border-bottom: 0.02rem solid lightgray;
        font-size: 0.24rem;
        width: 100%;
        height: 0.821rem;
        background-color: white;
        max-width: 6.4rem;
        display: flex;
        flex-flow:row nowrap;
        justify-content: center;
        .iconfont::before{
            font-size: 0.4rem;
        }
        .ts-info{
            margin-top: 0.15rem;
            text-align: center;        
            height: 0.521rem;
            line-height: 0.521rem;
            flex-grow: 1;
        }
        .ts-search{
            margin-top: 0.15rem;
            font-size: 0.25rem;
            text-indent: 0.2rem;
            height: 0.521rem;
            color:gray;
            background-color: #eee;
            line-height: 0.521rem;
            flex-grow: 8;
            border-radius: 0.1rem;
            .iconfont::before{
                font-size: 0.25rem;
            }
        }
        .ts-shop{
            margin-top: 0.1rem;
            text-align: center;
            height: 0.621rem;
            line-height: 0.621rem;
            flex-grow: 1;
        }
    }
</style>



