<template>
    <div class="mss-body">
        <div class="mss-history">
            <div class="mss-left">
                <i class="iconfont icon-ziyuan"></i>
                <span>热门搜索</span>
            </div>
        </div>
        <div class="mss-history-back">
            <div v-for="(v,i) of hotSearch" :key="i" class="mss-history-details" @click="doSearch(v.frontword)">
                <router-link :to="`/details/${v.frontword}`" tag="div">
                    <span :style="{color:v.color}">{{ v.frontword }}</span>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        computed:{
            hotSearch(){
                return this.$store.getters.hotSearch;
            }
        },
        methods:{
            doSearch(v){
                this.$store.commit("setQuestion",v);
            }
        }
    }
</script>

<style lang="less">
    .mss-body{
        width: 100%;
        margin-top: 0.2rem;
        font-size: 0.24rem;
        color: rgb(136, 131, 131);
        .mss-history{
            width: 100%;
            display: flex;
            flex-flow: row nowrap;
            .mss-left{
                text-indent: 5%;
                width: 50%;
            }
            .mss-right{
                width: 40%;
                text-align: right;
            }
        }
        .mss-history-back{
            width: 86%;
            margin-left:7%;
            display: flex;
            flex-flow: row wrap;
            .mss-history-details{
                padding: 0.02rem 0.1rem;
                margin:0.2rem  0.15rem 0 0;
                height: 0.3rem;
                border-radius: 0.1rem;
                border: 0.02rem solid lightgray;
            }
        }
    }
</style>



