import fetchJsonp from 'fetch-jsonp'

const homeUrl = "https://mce.mogucdn.com/jsonp/multiget/3";
const homeLikesUrl = "https://list.mogujie.com/search";
const typeUrl = "https://mce.mogujie.com/jsonp/makeup/3";
const keyUrl = "https://list.mogujie.com/module/mget";
const productUrl = "https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1";

function dealParams(params){
    let temp = "";
    for (let i in params){
        temp += `${i}=${params[i]}&`;
    }
    return temp.substring(0,temp.length-1);
}

const jsonp = {
    findDatasByPids(params){
        return fetchJsonp(homeUrl + "?" +dealParams(params))
                .then((response)=>{
                    return response.json()
                })
    },
    findDatasByParams(params){
        return fetchJsonp(homeLikesUrl + "?" +dealParams(params))
                .then((response)=>{
                    return response.json()
        })
    },
    findDatasByPid(params){
        return fetchJsonp(typeUrl + "?" +dealParams(params))
                .then((response)=>{
                    return response.json()
                })
    },
    findDataByKey(params){
        return fetchJsonp(keyUrl + "?" +dealParams(params))
                .then((response)=>{
                    return response.json()
                })
    },
    findCommentById(params){
        return fetchJsonp(productUrl + "?" +dealParams(params))
                .then((response)=>{
                    return response.json()
                })
    }
}

export default jsonp;


