<template>
    <div class="mtla-body">
        <div v-for="(v,i) of datas" :key="i" :class="{ ct : true , cts : cc == i }" @click="changeState(v.maitKey,v.miniWallkey,i)">
            {{ v.title }} 
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                cc:0
            }
        },
        methods:{
           changeState(par,par2,i){
                this.cc = i;
                this.$store.commit("setPid",par);
                this.$store.commit("setSortId",par2);
                this.$store.dispatch("loadTypeShow");
                this.$store.dispatch("loadTypeDetails");
           }
        },
        props:{
            datas:null
        }
    }
</script>

<style lang="less">
    .mtla-body{
        width: 100%;
        .ct{
            font-weight: 400;
            width: 100%;
            text-align: center;
            line-height: 0.8rem;
            height: 0.8rem;
            font-size: 0.25rem;
        }
        .cts{
                background-color: white;
                color: red;
            }
    }
</style>



