<template>
    <div class="msi-body">
        <div class="msi-left" @click="goBack()">
            <i class="iconfont icon-xiangzuo"></i>
        </div>
        <div class="msi-search">
            <input type="text" v-model="history" :placeholder="defaultWord">
        </div>
        <div class="msi-right" @click="searchData()">
            <router-link :to="`/details/${!!history?history:defaultWord}`" tag="span">
                搜索 
            </router-link>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                history:""
            }
        },
        computed:{
            defaultWord(){
                return this.$store.getters.defaultWord;
            }
        },
        methods:{
            goBack(){
                this.$router.go(-1);
            },
            searchData(){
                if(!!this.history){
                    this.$store.commit("setHistoryData",this.history);
                }
                this.$store.commit("setQuestion",this.history);
            }
        }
    }
</script>

<style lang="less">
    .msi-body{
        width: 100%;
        height: 0.821rem;
        font-size: 0.2rem;
        display: flex;
        flex-flow: row nowrap;
        line-height: 0.821rem;
        box-sizing: border-box;
        border-bottom:0.01rem solid #ccc;
        .msi-left{
            text-align: center;
            width: 10%; 
            .iconfont{
                color: rgb(130, 128, 128);
                font-size: 0.4rem;
            }
        }
        .msi-search{
            width: 78%;
            height: 100%;
            >input{
                margin-top: 0.2rem;
                font-size: 0.2rem;
                text-indent: 0.2rem;
                color:rgb(130, 128, 128);
                width: 99%;
                height: 0.421rem;
                border: 0.02rem solid lightgray;
                border-radius: 0.1rem; 
                outline: none;
            }
            >input[type=text]:focus{
                border: 0.02rem solid red;
            }
        }
        .msi-right{
            width: 12%;
            margin-top: 0.03rem;
            font-size: 0.26rem;
            text-align: center;
            color: rgb(130, 128, 128);
        }
    }
</style>



