<template>
    <div class="sd-body">
        <TopSearch></TopSearch>
        <div class="sd-class">
            <div v-for="(v,i) of sortsName" :key="i" :class='{ title:true,choose:i==num }' @click="changeNum(i,v.sortKey)">
                {{ v.title }}
            </div>
            <div class="title">
                价格
            </div>
        </div>
        <div class="sd-content" ref="wrrp">
            <div class="sd-wrap">
                <div v-for="(v,i) of searchDetails" :key="i" class="sd-detail">
                    <ProductList :datas="v"></ProductList>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Bscroll from 'better-scroll'

    export default {
        data(){
            return {
                num:0
            }
        },
        computed:{
            searchDetails(){
                return this.$store.getters.searchDetails;
            },
            sortsName(){
                return this.$store.getters.sortsName;
            }
        },
        methods:{
            bscrollInit(){
                this.$nextTick(()=>{
                    if(this.scroll){
                        this.scroll.refresh();
                    }else{
                        this.scroll = new Bscroll(this.$refs.wrrp,{
                            click:true
                        });
                    }
                });
            },
            changeNum(i,v){
                this.num = i;
                this.$store.commit("setSorts",v)
                this.$store.dispatch("loadSearchDetails");
            }
        },
        mounted(){
            this.bscrollInit();
            if(this.$store.getters.title){
                this.$store.dispatch("loadSearchDetail");
            }else{
                this.$store.dispatch("loadSearchDetails");
            }   
        }
    }
</script>

<style lang="less">
    .sd-body{
        width: 100%;
        .sd-class{
            width: 100%;
            height: 0.6rem;
            display: flex;
            flex-flow: row wrap;
            font-size: 0.3rem;
            .title{
                width: 25%;
                height: 0.6rem;
                line-height: 0.6rem;
                text-align: center;
                border: 0.01rem solid lightgray;
                border-top: 0;
                box-sizing: border-box;
            }
            .choose{
                color: red;
            }
        }
        .sd-content{
            width: 100%;
            position: absolute;
            top: 1.421rem;
            bottom: 0;
            left: 0;
            right: 0;
            
            .sd-wrap{
                display: flex;
                flex-flow: row wrap;
                justify-content: space-around;
                width: 100%;
                .sd-detail{
                    width: 50%;
                    font-size: 0.25rem;
                }
            }
        }
    }
</style>



